.bottom-menu{z-index:99;background-color:#2b2b2b}.bottom-menu ul.main{margin:0;padding:0;display:table;list-style:none;width:100%}.bottom-menu ul.main a{color:#b3b3b3;display:block;padding:.5rem 0}.bottom-menu ul.main li{text-align:center;display:table-cell;color:#b3b3b3}.bottom-menu .sub-menu{bottom:0;left:0;right:0;list-style:none;margin:0 1rem;padding:0;background-color:#ececec;opacity:.9;border-radius:.5rem}.bottom-menu .sub-menu li{text-align:center;color:#b3b3b3;display:block;padding:.5rem}.bottom-menu .sub-menu li.sub-menu__line{border-bottom:1px solid #797979}.bottom-menu .sub-menu li.sub-menu__version{font-size:.7rem}.bottom-menu__font-size{font-size:16px}