html{font-size:62.5%}body{margin:0;font-family:-apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,"Helvetica Neue",Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol";font-weight:400;line-height:1.5;font-size:1.4rem;color:#212529;text-align:left;background-color:#fff}html,body{background-color:#2b2b2b;height:100%;width:100%;color:#b2b2b2;overflow:hidden}#root{height:100%;width:100%}.app__status{color:#b3b3b3;font-size:.9rem}.app__opening{font-size:.6rem}.fen{display:none}.alpha-version{background-color:#007bff;color:#fff;font-size:.8rem;text-align:center;padding:.4rem}a{color:#007bff;transition:color .125s ease;text-decoration:none}a:hover{color:#4fa7ff;text-decoration:none}button{border:0;background-color:transparent;padding:0;cursor:pointer}ul{margin:0;padding:0}input{display:block;width:100%;padding:.2rem .4rem;font-size:.8rem;line-height:1.5;color:#7b7b7b;background-color:#fff;background-clip:padding-box;border:1px solid #ced4da;border-radius:.2rem;transition:border-color .15s ease-in-out,box-shadow .15s ease-in-out}table{width:100%;border-collapse:collapse}.level-0{font-size:18px}.level-1{font-size:16px}.level-2{font-size:14px}.level-3{font-size:11px}